.bubble {
  margin: 20px;
  width: 100px;
  height: 100px;
  background: radial-gradient(
    50% 50% at 50% 50%,
    rgba(0, 17, 57, 0.19) 33.65%,
    #02a5df 100%
  );
  border: 3px solid #02a5df;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;

  color: white;
  font-size: 2rem;
  font-weight: 700;

  animation: float 10s infinite;
  pointer-events: none;
}

.random {
  background: radial-gradient(
    50% 50% at 50% 50%,
    rgb(255, 33, 208, 0.25) 33.65%,
    rgb(255, 33, 208, 0.5) 100%
  );
  border: 3px solid var(--cl-pink);
  pointer-events: auto;
}

#section-bubbles {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  padding: 40px;
}

@keyframes float {
  25% {
    transform: translateY((random(30)) + px) translateX(random(30) + px);
  }

  50% {
    transform: translateY((random(30)) + px) translateX(random(30) + px);
  }

  75% {
    transform: translateY((random(30)) + px) translateX(random(30) + px);
  }
}
